@import "../../styles/partials/global";

.sidebar-container {
  margin: 104px 0 0 0;

  width: 0;
  height: 100vh;
  background: linear-gradient(
      217deg,
      rgba(10, 162, 231, 0),
      rgba(10, 162, 231, 0) 70.71%
    ),
    linear-gradient(336deg, rgba(10, 162, 231, 0), rgba(76, 243, 198, 0) 90.71%);
  position: fixed;
  top: 0;
  transition: 0.3s;
  // visibility:hidden ;
  display: none;
  box-shadow: 3px 4px 8px 0px rgba(0, 0, 0, 0.8);
  border-radius: 4px;
  @include tablet {
    margin: 104px 0 0 10px;
    height: 90vh;
  }
  @include desktop {
    width: 100%;
    height: 65px;
    top: 0;
    transition: 0.3s;
    background-color: transparent;
    margin: 0;
    // visibility: visible;
    display: flex;
    justify-content: flex-end;
    box-shadow: none;
  }

  &--active {
    @include mobile {
      margin: 104px 0 0 0;

      position: fixed;
      top: 0;
      z-index: 3;
      width: 100%;
      background: linear-gradient(
          217deg,
          rgba(10, 162, 231, 1),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(76, 243, 198, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );

      transition: left 0.3s ease-out, right 0.3s ease-out;
      opacity: 0.97;
    }
    @include tablet {
      position: fixed;
      top: 0;
      z-index: 3;
      width: 385px;
      background: linear-gradient(
          217deg,
          rgba(76, 243, 198, 0.8),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(10, 162, 231, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );

      transition: left 0.3s ease-out, right 0.3s ease-out;
      margin: 104px 0 0 10px;

      border-radius: 4px;
    }
    @include desktop {
      transition: 0.3s;
      width: 100%;
      background: linear-gradient(
          217deg,
          rgba(10, 162, 231, 1),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(10, 162, 231, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );
      margin: 0;
    }
  }

  &__sidebar-width {
    @include desktop {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 1300px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    padding: 0;
    height: 100vh;
    margin: 0;
    box-shadow: 3px 4px 8px 0px rgba(0, 0, 0, 0.8);
    &-link {
      cursor: pointer;
    }
    @include tablet {
      height: 87.5vh;
    }

    @include desktop {
      flex-direction: row;
      justify-content: flex-end;

      height: 95px;
      box-shadow: none;
    }
    &-item {
      position: relative;
      margin: 0 0 50px 0;
      @include desktop {
        margin: 0 20px 0 0;
      }
    }
    &-home,
    &-about,
    &-skills,
    &-work,
    &-contact {
      width: 40px;
      height: 40px;
      @include desktop {
        width: 20px;
        height: 20px;
        margin: 0 10px 0 0;
        display: none;
      }
    }
  }
  &__list-link {
    display: flex;
    align-items: flex-end;
    width: 220px;
    justify-content: flex-start;
    @include desktop {
      display: flex;
      text-decoration: none;
      width: auto;
      justify-content: flex-end;
    }
  }
  &__hover-home,
  &__hover-about,
  &__hover-skills,
  &__hover-projects,
  &__hover-contact {
    display: none;

    font-family: "RalewaySB";
    color: #444649;
    // background: linear-gradient(#ffffff, #38495a);
    // background-clip: text;
    // -webkit-text-fill-color: transparent;
    display: flex;
    font-size: 30px;
    font-weight: bold;
    padding: 0 0 0 30px;
    margin: 0;
    text-shadow: none;
    // text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.4), 0px 8px 13px rgba(0, 0, 0, 0.1),
    //   0px 18px 23px rgba(0, 0, 0, 0.1);
    &:hover {
      // color: #ffffff;
      text-shadow: none;
    }

    @include desktop {
      font-family: "RalewayB";
      color: #444649;
      display: flex;
      margin: 0;
      align-self: flex-end;
      // text-shadow: 0px 4px 3px rgba(0, 0, 0, 0.4),
      //   0px 8px 13px rgba(0, 0, 0, 0.1), 0px 18px 23px rgba(0, 0, 0, 0.1);
      font-size: 20px;
      // text-shadow: none;
      font-weight: bold;
      &:hover {
        color: #ffffff;
      }
    }
  }
