  &__backdrop {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 95px;
    left: 0;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.6);
    transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
    @include desktop {
      display: none;
    }
  }
  &__backdrop-false {
    display: none;
  }
  &__social-container {
    width: 100px;
    @include desktop {
      display: none;
    }
  }
  &__linkedin-image {
    width: 40px;
    height: 40px;
    &:hover {
      cursor: pointer;
    }
  }
  &__github-image {
    width: 35px;
    height: 35px;
    &:hover {
      cursor: pointer;
    }
  }
  &__top-hr {
    border-radius: 11px;
    border: 1px solid #444649;
  }
  &__linkedin-image {
    margin: 0 10px 0 0;
  }
  &__github-image {
    margin: 0 0 0 10px;
  }
  &__flex-list {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      display: flex;
      flex-direction: row;
    }
  }
}
